// src/pages/Perfil.jsx
/* global Swal */
import { useEffect, useRef, useState } from "react";
import Cropper from "react-easy-crop";
import "./Perfil.css";

function Perfil() {
  const nombreUsuario =
    localStorage.getItem("nombreUsuario") || "Nombre Apellido";
  const email = localStorage.getItem("emailUsuario") || "correo@ejemplo.com";
  const tipo = localStorage.getItem("userType") || "Pastor";
  const defaultImg = "./assets/default-profile.png";
  const [edit, setEdit] = useState(false);
  const [nombre, setNombre] = useState(nombreUsuario);
  const [correo, setCorreo] = useState(email);
  const [fotoPerfil, setFotoPerfil] = useState(
    localStorage.getItem("logoPerfil") || defaultImg
  );
  const [showCropper, setShowCropper] = useState(false);
  const [crop, setCrop] = useState({ x: 0, y: 0 });
  const [zoom, setZoom] = useState(1);
  const [croppedAreaPixels, setCroppedAreaPixels] = useState(null);
  const [imageSrc, setImageSrc] = useState(null);
  const [imgLoading, setImgLoading] = useState(true);
  const [imgVisible, setImgVisible] = useState(false);
  const fileInputRef = useRef(null);

  useEffect(() => {
    setImgLoading(true);
    setImgVisible(false);
  }, [fotoPerfil]);

  function resizeImage(file, maxSize = 300) {
    return new Promise((resolve) => {
      const img = new window.Image();
      const reader = new FileReader();
      reader.onload = (e) => {
        img.onload = () => {
          const canvas = document.createElement("canvas");
          let width = img.width;
          let height = img.height;
          if (width > height) {
            if (width > maxSize) {
              height *= maxSize / width;
              width = maxSize;
            }
          } else {
            if (height > maxSize) {
              width *= maxSize / height;
              height = maxSize;
            }
          }
          canvas.width = width;
          canvas.height = height;
          const ctx = canvas.getContext("2d");
          ctx.drawImage(img, 0, 0, width, height);
          resolve(canvas.toDataURL("image/jpeg", 0.8));
        };
        img.src = e.target.result;
      };
      reader.readAsDataURL(file);
    });
  }

  // Cuando el usuario selecciona una imagen
  const handleFileChange = async (e) => {
    const file = e.target.files?.[0];
    if (!file) return;

    // Validar tipo de archivo
    if (!file.type.startsWith("image/")) {
      Swal.fire({
        icon: "error",
        title: "Archivo no permitido",
        text: "Solo puedes subir archivos de imagen (jpg, png, etc.)",
        confirmButtonColor: "#888", // Cambia aquí el color del botón OK
      });
      if (fileInputRef.current) fileInputRef.current.value = "";
      return;
    }

    const reader = new FileReader();
    reader.onload = () => {
      setImageSrc(reader.result);
      setShowCropper(true);
    };
    reader.readAsDataURL(file);
  };

  // Cuando el usuario ajusta el crop
  const onCropComplete = (croppedArea, croppedAreaPixels) => {
    setCroppedAreaPixels(croppedAreaPixels);
  };

  // Recorta la imagen y la sube
  const getCroppedImg = async () => {
    const createImage = (url) =>
      new Promise((resolve, reject) => {
        const image = new window.Image();
        image.addEventListener("load", () => resolve(image));
        image.addEventListener("error", (error) => reject(error));
        image.setAttribute("crossOrigin", "anonymous");
        image.src = url;
      });

    const image = await createImage(imageSrc);
    const canvas = document.createElement("canvas");
    const ctx = canvas.getContext("2d");

    const { width, height, x, y } = croppedAreaPixels;
    canvas.width = width;
    canvas.height = height;
    ctx.drawImage(image, x, y, width, height, 0, 0, width, height);

    return new Promise((resolve) => {
      canvas.toBlob((blob) => {
        resolve(blob);
      }, "image/jpeg");
    });
  };

  const handleCropSave = async () => {
    const croppedBlob = await getCroppedImg();
    const usuarioId = localStorage.getItem("usuario_id");
    if (!usuarioId || usuarioId === "null") {
        Swal.fire({
        icon: "error",
        title: "Usuario no encontrado",
        text: "No se encontró el ID de usuario. Por favor, inicia sesión de nuevo." + 555,
        confirmButtonColor: "#888", // este es el color del botón "OK"
      });
      return;
    }
    const formData = new FormData();
    formData.append("usuario_id", usuarioId);
    formData.append("foto", croppedBlob, "perfil.jpg");

    const response = await fetch("http://localhost:5000/api/upload_foto", {
      method: "POST",
      body: formData,
    });
    const data = await response.json();
    if (data.success) {
      const nuevaUrl = `http://localhost:5000/uploads/${
        data.foto_perfil
      }?t=${Date.now()}`;
      setFotoPerfil(nuevaUrl);
      localStorage.setItem("logoPerfil", nuevaUrl);
      window.dispatchEvent(new Event("logoPerfilChanged"));
      setShowCropper(false);
      setImageSrc(null);
      if (fileInputRef.current) fileInputRef.current.value = "";
    }
  };

  const handleSave = () => {
    localStorage.setItem("nombreUsuario", nombre);
    localStorage.setItem("emailUsuario", correo);
    setEdit(false);
  };

  return (
    <div className="perfil-outer-container">
      <div className="perfil-card">
        <div className="perfil-foto-section">
          <div className="perfil-foto-grande">
            <img
              src={fotoPerfil}
              alt="Foto de perfil"
              className={`perfil-foto-fadein${imgVisible ? " visible" : ""}`}
              style={{ display: imgLoading ? "none" : "block" }}
              onLoad={async () => {
                // Espera un pequeño tiempo para asegurar que el loader se quite antes del fade
                setImgLoading(false);
                setTimeout(() => setImgVisible(true), 100); // 100ms para que el fade sea visible
              }}
              onError={(e) => {
                e.target.onerror = null;
                e.target.src = defaultImg;
                setImgLoading(false);
                setImgVisible(true);
              }}
            />
            {imgLoading && (
              <div
                className="loader-img-css"
                style={{ width: 60, height: 60 }}
              ></div>
            )}
            <label className="upload-label">
              <input
                className="input-file"
                type="file"
                hidden
                ref={fileInputRef}
                accept="image/*" // <-- Solo permite imágenes
                onChange={handleFileChange}
              />
              Cambiar foto
            </label>
          </div>
          {/* Cropper modal */}
          {showCropper && (
            <div className="cropper-modal">
              <div className="cropper-wrapper">
                <div className="cropper-title">Ajusta tu foto de perfil</div>
                <div
                  style={{
                    position: "relative",
                    width: "100%",
                    height: "350px",
                  }}
                >
                  <Cropper
                    image={imageSrc}
                    crop={crop}
                    zoom={zoom}
                    aspect={1}
                    onCropChange={setCrop}
                    onZoomChange={setZoom}
                    onCropComplete={onCropComplete}
                  />
                </div>
                <div className="cropper-controls">
                  <span style={{ color: "#555", fontSize: "0.97rem" }}>
                    Zoom
                  </span>
                  <input
                    className="cropper-slider"
                    type="range"
                    min={1}
                    max={3}
                    step={0.01}
                    value={zoom}
                    onChange={(e) => setZoom(Number(e.target.value))}
                  />
                </div>
                <div className="cropper-btns">
                  <button className="guardar" onClick={handleCropSave}>
                    Guardar
                  </button>
                  <button
                    className="limpiar"
                    onClick={() => setShowCropper(false)}
                  >
                    Cancelar
                  </button>
                </div>
              </div>
            </div>
          )}
          {/* Fin cropper */}
          <div className="perfil-nombre-rol">
            <h2>{nombre}</h2>
            <span className="perfil-rol">{tipo}</span>
          </div>
        </div>
        <div className="perfil-info-section">
          <div className="perfil-info-row">
            <label>Nombre</label>
            <input
              value={nombre}
              disabled={!edit}
              maxLength={50}
              onChange={(e) => setNombre(e.target.value)}
              className="perfil-input"
            />
          </div>
          <div className="perfil-info-row">
            <label>Email</label>
            <input
              value={correo}
              disabled={!edit}
              maxLength={50}
              onChange={(e) => setCorreo(e.target.value)}
              className="perfil-input"
            />
          </div>
          <div className="perfil-info-row">
            <label>Rol</label>
            <input value={tipo} disabled className="perfil-input" />
          </div>
        </div>
        <div className="perfil-botones" style={{ justifyContent: "center" }}>
          {edit ? (
            <>
              <button className="guardar" onClick={handleSave}>
                Guardar
              </button>
              <button className="limpiar" onClick={() => setEdit(false)}>
                Cancelar
              </button>
            </>
          ) : (
            <button className="editar" onClick={() => setEdit(true)}>
              Editar información
            </button>
          )}
        </div>
      </div>
    </div>
  );
}

export default Perfil;
